@media screen and (max-width: 768px) {

    .paginaCadastroContainer {
        display: flex;
        flex-direction: column-reverse;
        /*align-items: center;*/
        margin-left: 5%;
        margin-top: 10%;
        height: 700px;
    }

    .paginaCadastroLadoEsquerdo {
        background-image: url('../../images/cadastrofundomobile.svg');
        height: 50%;
        background-repeat: no-repeat;
        background-position: bottom left;
        margin-left: -5%;
    }

    .paginaCadastroLadoDireito {
        width: 90%;
    }

    .paginaCadastroTextoTitulo {
        font-family: Montserrat;
        font-style: normal;
        font-weight: bold;
        font-size: 22px;
        line-height: 33px;
        color: #212529;
    }

    .paginaCadastroFormulario {
        height: 40%;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
    }

    .paginaCadastroTextoSubtitulo {
        font-family: Open Sans;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 24px;
        display: flex;
        align-items: center;
        color: #343A40;
    }

    .paginaCadastroFormularioInput {
        width: 100%;
        border-bottom: 1px solid #ADB5BD;
        border-width: 0 0 2px;
        font-family: Open Sans;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 24px;
        display: flex;
        align-items: center;
        color: #ADB5BD;
        padding: 5px;
    }

    .paginaCadastroFormularioTermo {
        display: flex;
        margin: 5% 0 5% 0;
    }

    .paginaCadastroFormularioTermoString {
        font-family: Open Sans;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 18px;
        display: flex;
        align-items: center;
        color: #868E96;
    }

    .paginaCadastroFormularioBotao {
        width: 100%;
        height: 40px;
        left: 35px;
        top: 575px;
        background: #71CAD4;
        border-radius: 4px;
        justify-content: center;
        font-family: Open Sans;
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 24px;
        display: flex;
        align-items: center;
        text-align: center;
        color: #FFFFFF;
    }

}

@media screen and (min-width: 769px) {

    .paginaCadastroContainer {
        height: 600px;
        display: flex;
    }

    .paginaCadastroLadoEsquerdo {
        background-image: url('../../images/cadastro.svg');
        width: 50%;
        background-repeat: no-repeat;
        background-position: bottom left;
    }

    .paginaCadastroLadoDireito {
        width: 50%;
    }

    .paginaCadastroLadoDireito {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
    }

    .paginaCadastroTexto {
        width: 55%;
    }

    .paginaCadastroTextoTitulo {
        font-family: Montserrat;
        font-style: normal;
        font-weight: bold;
        font-size: 22px;
        line-height: 33px;
        color: #212529;
    }

    .paginaCadastroTextoSubtitulo {
        font-family: Open Sans;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 24px;
        display: flex;
        align-items: center;
        color: #343A40;
        margin-top: 2%;
    }

    .paginaCadastroFormulario {
        display: flex;
        flex-direction: column;
        height: 30%;
        justify-content: space-around;
        margin: 4% 0 4% 0;
    }

    .paginaCadastroFormularioInput {
        width: 520px;
        border-bottom: 1px solid #ADB5BD;
        border-width: 0 0 2px;
        font-family: Open Sans;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 24px;
        display: flex;
        align-items: center;
        color: #ADB5BD;
        padding: 5px;
    }

    .paginaCadastroFormularioInput::placeholder {
        color: #ADB5BD;
    }

    .paginaCadastroFormularioTermo {
        display: flex;
        margin: 1% 0 1% 0;
    }

    .paginaCadastroFormularioTermoString {
        font-family: Open Sans;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 18px;
        display: flex;
        align-items: center;
        color: #868E96;
    }

    .paginaCadastroFormularioBotaoEnviarContainer {
        margin-top: 2%;
    }

    .paginaCadastroFormularioBotao {
        width: 521px;
        height: 40px;
        left: 0px;
        top: 0px;
        background: #71CAD4;
        border-radius: 4px;
        justify-content: center;
        font-family: Open Sans;
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 24px;
        display: flex;
        align-items: center;
        text-align: center;
        color: #FFFFFF;
        cursor: pointer;
        transition: ease 0.4s;
      }
      
      .paginaCadastroFormularioBotao:hover {
        background-color: #1F99A7;
      }

}